const initialState = {
    city: null,
    weather: null
}

export const Reducer = (state = initialState, actions) => {
    switch (actions.type) {
        case 'CHANGE_CITY':
            return {
                city: actions.city
            }
        case 'CHANGE_WEATHER':
            return {
                weather: actions.weather
            }
        default: return state
    }
}